body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.inputField {
    padding: 5px;
    width: 200px;
    margin: 10px;
}

.searchSubmitButton {
    margin: 10px;
    height: 25px;
    
    width: 70px;
    background-color: white;
    border-color: lightgrey;
}

.searchSubmitButton:hover {
    cursor: pointer;
    background-color: rgb(240, 59, 179);
    color: white;
}
.FilterOptions {
    display: -webkit-flex;
    display: flex;
}

.filter_bookType {
    margin-left: 20px;
}

.Dropdown_label {
    margin: 10px;
}

.BookResult {
    display: -webkit-flex;
    display: flex;
    width: 900px;
    padding: 20px;
    margin: 20px;
    
}

h1 {
    margin-top: 0;
    line-height: 16px;
}

.book_textContent {
    float: 2;
    width: 400px;
}

.book_thumbnail {
    float: 1;
    width: 200px;
    overflow: hidden;
}

.book_title{
    font-size: 24px;
    color:black;
    text-decoration: none;
}

.book_title:hover {
    color: blue;
    text-decoration: underline;
}

.book_author{
    font-size: 18px;
    font-weight: 300;
}

p {
    font-size: 15px;
}
.filters-div {
    padding-left: 20px;
    margin: 20px;
}

.search-div {

        padding-left: 20px;
        margin: 20px;
}
.app_header_text {

}

.app_header {
  padding-left: 20px;
  margin: 20px;
}

.error {
  padding-left: 20px;
  margin: 20px;
  padding-top: 40px;
  width: 500px;
}
